body {
    margin: 0;
  }
  h1, h2, h3, h4, h5, p, ul, li {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }

  a {
    text-decoration: none;
    color: rgb(124, 124, 124);
  }

  a:hover {
    color: #000;
    transition-duration: 0.6s;
  }
  
  img {
    margin-top: 40px;
    margin-bottom: 40px;
    width: 900px;
  }
  
  h2 {
    font-size: 20px;
    font-weight: 800px;
  }

  h3 {
    margin-top: 2em;
    font-size: 20px;
    font-weight: 400px;
    color: rgb(80, 80, 80);
  }

  h4 {
    font-size: 20px;
    font-weight: 100px;
  }

  h5 {
    font-size: 12px;
    color: rgb(54, 54, 54);
  }

  h1 {
    font-size: 2.6em;
    font-weight: 300;
    width: 800px;
    color: rgb(46, 46, 46);
    margin-left: auto;
    margin-right: auto;
  }
  .content {
    background-color: #FFF;
    padding: 10px;
    margin-left: auto;
    margin-right: auto;
  }
  .content h2 {
    padding: 0;
    margin: 0;
  }
  .content li {
    margin-bottom: 10px;
  }
  .active {
    color: rgb(0, 0, 0);
  }
  #main {
    width: 900px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;
  }
  #container {
      /* position: relative; */
      display: flex;
      flex-wrap: wrap;
      width: 800px;
      /* height: 700px; */
      /* background-color: black; */
      margin-left: auto;
      margin-right: auto;
      padding: 10px;
  }

  .el {
    width: 380px;
    height: 380px;
    margin: 10px;
    background-color: rgb(0, 0, 0);
    transition: .5s ease;
    
  }


  /* .sub_el {
    width: 380px;
    height: 380px;
    margin: 10px;
    background-color: rgb(0, 0, 0);
    display: none;

  } */

  .el:hover {
    opacity: 0.8;
    /* display:block; */
  }


  .menu {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    float: right;
    margin-top: 20px;
    font-weight: 700;
    font-size: 0.9em;
    word-spacing: 2em;    
  }

  /* .menu a {
    color: rgb(206, 7, 7);
    word-spacing: 2em;
  } */

  .logo {
    display: inline-block;
    letter-spacing: -0.8px;
    color: rgb(46, 46, 46);
    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
  }

  .header {
    width: 900px;
    display: inline-block; 
  }

  #up_margin {
    height: 30px;
  }

  #down_margin {
    height: 100px;
  }

p {
  color: rgb(43, 43, 43);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 0.9rem;
  line-height: 1.3rem;
}

#nf {
  background: url('./img/nf_pres.png') no-repeat;
  background-size: 380px 380px;
}

#spbtv {
  background: url('./img/spbtv_pres.png') no-repeat;
  background-size: 380px 380px;
}

#amediateka {
  background: url('./img/amediateka_pres.png') no-repeat;
  background-size: 380px 380px;
}

#skemio {
  background: url('./img/skemio_pres.png') no-repeat;
  background-size: 380px 380px;
}

#wispi {
  background: url('./img/wispi_pres.gif') no-repeat;
  background-size: 380px 380px;
}

#unselect {
  margin-top: 40px;
  margin-bottom: 40px;
  -webkit-user-select: none; /* Safari */        
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */
}

#me {
  display: table-cell;
  background: url('./img/me.png') no-repeat;
  background-size: 350px 438px;
  width: 350px;
  height: 438px;
  padding: 20px;
}

#table {
  display: table;
}

#table a {
  display: inline-block;
  
}

#cell {
  display: table-cell;
  padding: 20px;
}

#button_left {
  display: table-cell;
  border-radius: 24px;
  border: 2px solid rgb(41, 41, 41);
  width: 120px;
  height: 40px;
  text-align: center;
  vertical-align: middle;
  line-height: 40px;
  color: #000;
  font-weight: 500;
}

#button_left:hover {
  border-radius: 24px;
  background: rgb(41, 41, 41);
  color: #fff;

}

#linkedin {
  display: table-cell;
  background: url('./img/linkedin.png') no-repeat;
  background-size: 40px 40px;
  height: 40px;
  width: 40px;
}

#info {
  padding: 0.5em;
  margin: 2em;
  background-color: rgb(255, 249, 217);
}

.a_color {
  color: #000;
  font-weight: 600;
  text-decoration: underline;
}

.a_tab {
  margin-right: 20px;
}